//type
type StateType = {
    [key: string]: {
        headerComponent: HeaderComponentType
        mainComponent: MainComponentType
        aboutMeComponent: AboutMeComponent
        skillsComponent: SkillsComponentsType
        projectsComponent: ProjectsComponentsType
        distantWorkComponent: DistantWorkComponentType
        contactsComponent: ContactsComponentType
        footerComponent: FooterComponentType
    }
}

export type HeaderComponentType = {
    menuName: string
    nav: NavType[]
    headerLinks: LinksType[]
}

export type NavType = {
    href: HrefNavType
    navName: string
}

type HrefNavType = 'home' | 'skills' | 'projects' | 'contacts' | 'about'

export type LinksType = {
    href: string
    svgName: LinksNameType
}

export type MainComponentType = {
    h1: string
    arrSkills: string[]
    location: string
    a1: string
    a2: string
    photo: string
}

export type AboutMeComponent = {
    span: string
    title: string
    h2: string
    name: string
    aboutMe: string
    contacts: ContactsType[]
}

type ContactsType = {
    title: string
    info: string
}

export type SkillsComponentsType = {
    span: string
    title: string
    skills: SkillType[]
}

type SkillType = {
    svgName: SkillsNameType
    description: string
}

export type ProjectsComponentsType = {
    span: string
    title: string
    projects: ProjectType[]
}

type ProjectType = {
    title: string
    img: string
    description: string
    href: string
}

export type DistantWorkComponentType = {
    title: string
    a: string
    bgImg: string
}

export type ContactsComponentType = {
    title: string
    p: string
    h3: string
    call: string
    contactsLinks: LinksType[]
    name: string
    errorName: string
    email: string
    errorEmail: string
    errorInvalidEmail: string
    formMessage: string
    errorMessage: string
    button: string
    myMessages: string
    error: string
}

export type FooterComponentType = {
    copyright: string
    designed: string
}